<template>
  <div>
    <nav class="navbar navbar-default">
      <div class="navbar-header">
        <button type="button" class="navbar-toggle collapsed" data-toggle="collapse" data-target="#bs-example-navbar-collapse-1" aria-expanded="false">
          <span class="sr-only">Toggle navigation</span>
          <span class="icon-bar"></span>
          <span class="icon-bar"></span>
          <span class="icon-bar"></span>
        </button>
        <a class="navbar-brand" href="#"></a>
      </div>

      <form class="navbar-form navbar-left" role="search">
        <div class="form-group">
          <input type="text" class="form-control" placeholder="" v-model="critiria">
        </div>
      </form>

      <p class="navbar-text navbar-left"></p>
    </nav>

    <div class="row">
      <member
        v-for="member in showMembers"
        :member="member"
        :index="$index">
      </member>
    </div>

  </div>
</template>

<script>
import service from '../service'
import Member from './Member.vue'

module.exports = {

  name: 'MembersView',

  components: {
    Member
  },

  data() {
    return {
      members: {},
      critiria: ''
    }
  },

  methods: {
    getMembers() {
      return service.getMembers().then(members => {
        this.members = members
      })
    }
  },

  computed: {
    showMembers () {
      return this.members.filter((member) => {
        return member.nickname.indexOf(this.critiria) > -1
      })
    }
  },

  route: {
    data () {
      this.getMembers().catch(() => {
        this.$router.go('/login');
      })
    }
  },
}
</script>


